@import "vars.scss";
@import "global.scss";
@import "fonts.scss";

i {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon-Moderno' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
}

body {
    color: $black;
    font-family: "Gilroy";
    font-weight: 400;
    font-size: 14px;
}

.container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 15px;
}

.container--fluid {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 30px;
}



.header {
    padding: 25px 0;
    background-image: url('../images/bgc.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
}

.menu__list {
    
    &-item {
        margin-left: 40px;
    } 

    &-link {
        display: block;
        color: #fff;

        &:hover {
            color: $-color--gold;
        }
    }
}

.logo__svg {
    width: 216px;
    height: 52px;
    fill: $-color--gold;
}

.header__top-btn {
    &:hover span{
        
        color: $-color--gold;
    }

    &--search {
        background-color: transparent;
        border:none;
        cursor: pointer;
    }

    &--text {
        margin-left: 16px;
        color: #fff;
    }
}

#form {
   
    padding: 20px;
    background-color: #000;
    display: none;
}

.navigation__icon {
    width: 18px;
    height: 18px;
}

@import "media.scss";