@font-face {
  font-family: 'Gilory';
  src: url('../fonts/Gilory-Regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/Gilory-Regular.woff') format('woff'), /* Pretty Modern Browsers */
       url('webfont.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('webfont.svg#svgFontName') format('svg'); /* Legacy iOS */
       font-style: normal;
       font-weight: 400;
       display: swap;
};

