html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.flex {
  display: flex;
}

.f-aligncenter {
  display: flex;
  align-items: center;
}

.d-spacebeetwen {
  display: flex;
  justify-content: space-between;
}

.j-center {
  display: flex;
  justify-content: center;
}

@font-face {
  font-family: 'Gilory';
  src: url("../fonts/Gilory-Regular.woff2") format("woff2"), url("../fonts/Gilory-Regular.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 400;
  display: swap;
}

@font-face {
  font-family: 'Quicksand';
  src: url("../fonts/Gilroy-SemiBold.woff2") format("woff2"), url("../fonts/Gilroy-SemiBold.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 600;
  display: swap;
}

@font-face {
  font-family: 'Quicksand';
  src: url("../fonts/Quicksand-Bold.woff2") format("woff2"), url("../fonts/Quicksand-Bold.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 700;
  display: swap;
}

@font-face {
  font-family: 'icomoon-Moderno';
  src: url("../fonts/icomoon-Moderno.eot?exr80l");
  src: url("../fonts/icomoon-Moderno.eot?exr80l#iefix") format("embedded-opentype"), url("../fonts/icomoon-Moderno.ttf?exr80l") format("truetype"), url("../fonts/icomoon-Moderno.woff?exr80l") format("woff"), url("../fonts/icomoon-Moderno.svg?exr80l#icomoon-Moderno") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon-Moderno' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
}

body {
  color: #263238;
  font-family: "Gilroy";
  font-weight: 400;
  font-size: 14px;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.container--fluid {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

.header {
  padding: 25px 0;
  background-image: url("../images/bgc.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
}

.menu__list-item {
  margin-left: 40px;
}

.menu__list-link {
  display: block;
  color: #fff;
}

.menu__list-link:hover {
  color: #ffd700;
}

.logo__svg {
  width: 216px;
  height: 52px;
  fill: #ffd700;
}

.header__top-btn:hover span {
  color: #ffd700;
}

.header__top-btn--search {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.header__top-btn--text {
  margin-left: 16px;
  color: #fff;
}

#form {
  padding: 20px;
  background-color: #000;
  display: none;
}

.navigation__icon {
  width: 18px;
  height: 18px;
}

@media (max-width: 720px) {
  .container--fluid {
    padding: 0 15px;
  }
}
